const DEFAULT = 'default'
const WARNING = 'warn'
const ERROR = 'error'
const SUCCESS = 'success'
const FATAL = 'fatal'
export default {
  level: {
    DEFAULT,
    WARNING,
    ERROR,
    SUCCESS,
    FATAL
  },
  position: {
    TOP: 'top',
    CENTER: 'center',
    BOTTOM: 'bottom',
    TOP_LEFT: 'topLeft',
    TOP_RIGHT: 'topRight',
    BOTTOM_LEFT: 'bottomLeft',
    BOTTOM_RIGHT: 'bottomRight'
  },
  levelFilter (text) {
    let result = ''
    switch (text) {
      case SUCCESS: result = ''; break
      case DEFAULT: result = ''; break
      case WARNING: result = ''; break
      case ERROR: result = ''; break
      case FATAL: result = ''; break
      default: result = ''
    }
    return result
  }
}
